import React, { Component, Suspense } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {
  setTampungCiok,
  settampungRongsok,
  setTotalNetto,
  setTotalRp,
} from "../actions/pembayaranSupplierAction";
import Swal from "sweetalert2";
import { getUUID } from "../actions/fakturAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
import Skeleton from "react-loading-skeleton";
import { finishSend, progressSend } from "../actions/LogicAction";
import HeaderCard from "../HeaderCard";
import { AxiosMasterGet, AxiosMasterPost } from "../Axios";
import PembayaranCustomerHead from "./PembayaranCustomerHead";
import {
  formatGram,
  formatGram2,
  formatRp,
  getUserID,
  multipleDeleteLocal,
} from "../helper";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import paginationFactory from "react-bootstrap-table2-paginator";
import { Field, reduxForm } from "redux-form";
import { renderFieldSelect } from "../renderField";
import { getcustomerList } from "../actions/customerAction";
import { getToday } from "../getDate";
import { setTitipCustomer } from "../actions/pembayaranCustomerAction";

const maptoState = (state) => {
  return {
    pembayaranSupplierList: state.provinsi.PembayaranCustomer,
    tampungCiok: state.provinsi.tampungCiok,
    tampungRongsok: state.provinsi.tampungRongsok,
    totalCiok: state.provinsi.totalCiok,
    totalRongsok: state.provinsi.totalRongsok,
    totalBrutoCiok: state.provinsi.totalBrutoCiok,
    totalBrutoRongsok: state.provinsi.totalBrutoRongsok,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    customer: state.provinsi.customer,
    onSend: state.provinsi.onSend,
    customerSelected: state.form.PembayaranCustomer?.values?.customer,
  };
};

class PembayaranCustomer extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      tanggal: formatedDate,
      bonBayar: "",
      detail: "",
      tipeTransaksi: "BARANG",
      formPembayaran: "row d-none",
      pilihBon: "col-lg-12",
      total_netto: 0,
      total_rp: 0,
      listBon: [],
      columns: [
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "sisa_piutang_netto",
          text: "Piutang Netto",
          align: "right",
          headerAlign: "right",
          formatter: (data) => {
            return formatGram(data, 3);
          },
        },
        {
          dataField: "sisa_piutang_rp",
          text: "Piutang Rupiah",
          align: "right",
          headerAlign: "right",
          formatter: (data) => {
            return formatRp(data);
          },
        },
        {
          dataField: "titip_gram",
          text: "Titip Gram",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "titip_rp",
          text: "Titip Rupiah",
          align: "right",
          headerAlign: "right",
        },
      ],
    };
  }
  componentDidMount() {
    this.props.dispatch(setTampungCiok());
    this.props.dispatch(settampungRongsok());
    this.props.dispatch(getUUID());
    this.props.dispatch(getcustomerList());
    localStorage.removeItem("FakturTerpilih");
  }

  componentWillUnmount() {
    multipleDeleteLocal([
      "PrintPembayaranSupplier",
      "totalTunaiRp",
      "alatBayarTunai",
      "totalTrfRp",
      "alatBayarTrf",
      "ciokTemp",
      "alatBayarCiok",
      "totalCiok",
      "totalBrutoCiok",
      "rongsokTemp",
      "alatBayarRosok",
      "totalRongsok",
      "totalBrutoRongsok",
      "listHutang",
      "discount",
      "bonus",
      "denda",
      "cashTempRp",
      "alatBayarTunaiRp",
      "transferTempRp",
      "alatBayarTrfRp",
      "noFaktur",
      "alatBayarGiroRp",
      "totalGiroRp",
      "alatBayarGiro",
      "totalGiro",
      "FakturTerpilih",
      "total_netto",
      "total_rp",
    ]);
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handleSubmit(data) {
    if (data.noBon === undefined) {
      warning("Mohon isi nomor bon");
      return false;
    } else if (data.tanggal === undefined) {
      warning("Mohon isi Tanggal");
      return false;
    } else if (
      localStorage.getItem("alatBayarCiok") === null &&
      localStorage.getItem("alatBayarRosok") === null &&
      localStorage.getItem("alatBayarTrf") === null &&
      localStorage.getItem("alatBayarTunai") === null &&
      localStorage.getItem("alatBayarTunaiRp") === null &&
      localStorage.getItem("alatBayarTrfRp") === null &&
      localStorage.getItem("alatBayarGiro") === null &&
      localStorage.getItem("alatBayarGiroRp") === null
    ) {
      warning("Mohon Masukan Alat Pembayaran Paling Tidak 1 Jenis");
      return false;
    }
    var RosokKosong = {
      kode_jenis: "-",
      bruto: 0,
      kadar_transaksi: 0,
      netto: 0,
    };
    var cashKosong = {
      cash_rp: 0,
      harga_emas: 0,
      cash_netto: 0,
    };
    var titipKosong = {
      titip_rp: 0,
      harga_emas: 0,
      titip_netto: 0,
    };
    var transferKosong = {
      transfer_rp: 0,
      harga_emas: 0,
      transfer_netto: 0,
      tanggal_transfer: "2021-01-01",
      acc_from: "-",
      acc_to: "-",
    };
    var giroKosong = {
      giro_rp: 0,
      harga_emas: 0,
      giro_netto: 0,
      tanggal_giro: "-",
      no_giro: "-",
      bank_giro: "-",
      acc_to: "-",
      bank_to: "-",
    };
    var bayarCiok = JSON.parse(localStorage.getItem("alatBayarCiok")) || [
      RosokKosong,
    ];
    var bayarRosok = JSON.parse(localStorage.getItem("alatBayarRosok")) || [
      RosokKosong,
    ];
    var bayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf")) || [
      transferKosong,
    ];
    var bayarTrfRp = JSON.parse(localStorage.getItem("alatBayarTrfRp")) || [
      transferKosong,
    ];
    var bayarTunai = JSON.parse(localStorage.getItem("alatBayarTunai")) || [
      cashKosong,
    ];
    var bayarTunaiRp = JSON.parse(localStorage.getItem("alatBayarTunaiRp")) || [
      cashKosong,
    ];
    var bayarTitip = JSON.parse(localStorage.getItem("alatBayarTitip")) || [
      titipKosong,
    ];
    var bayarTitipRp = JSON.parse(localStorage.getItem("alatBayarTitipRp")) || [
      titipKosong,
    ];
    var bayarGiro = JSON.parse(localStorage.getItem("alatBayarGiro")) || [
      giroKosong,
    ];
    var bayarGiroRp = JSON.parse(localStorage.getItem("alatBayarGiroRp")) || [
      giroKosong,
    ];
    var isi = {
      kode_customer: this.props.customerSelected,
      no_bon: data.noBon,
      no_bayar: this.props.noFaktur,
      tanggal: data.tanggal,
      type_bon: this.state.tipeTransaksi === "RUPIAH" ? "BON RP" : "BON GR",
      total_putus_titip: this.state.tipeTransaksi === "RUPIAH"
      ? bayarTitipRp
          .map((list) => list.titip_netto)
          .reduce((a, b) => a + b, 0)
      : localStorage.getItem("totalTitipRp") || 0,
      total_denda: data.denda || 0,
      jumlah_piutang:
        this.state.tipeTransaksi === "RUPIAH"
          ? data.totalHutangRP
          : data.totalHutangGr.replace(",", ""),
      total_piutang:
        this.state.tipeTransaksi === "RUPIAH"
          ? data.totalHutangRP
          : data.totalHutangGr.replace(",", ""),
      detail_ciok: bayarCiok,
      bayar_ciok: localStorage.getItem("totalCiok") || 0,
      detail_rongsok: bayarRosok,
      bayar_rongsok: localStorage.getItem("totalRongsok") || 0,
      detail_cash:
        this.state.tipeTransaksi === "RUPIAH" ? bayarTunaiRp : bayarTunai,
      bayar_cash:
        this.state.tipeTransaksi === "RUPIAH"
          ? bayarTunaiRp
              .map((list) => list.cash_netto)
              .reduce((a, b) => a + b, 0)
          : localStorage.getItem("totalTunaiRp") || 0,
      detail_titip:
        this.state.tipeTransaksi === "RUPIAH" ? bayarTitipRp : bayarTitip,
      bayar_titip:
        this.state.tipeTransaksi === "RUPIAH"
          ? bayarTitipRp
              .map((list) => list.titip_netto)
              .reduce((a, b) => a + b, 0)
          : localStorage.getItem("totalTitipRp") || 0,
      detail_trf: this.state.tipeTransaksi === "RUPIAH" ? bayarTrfRp : bayarTrf,
      bayar_trf:
        this.state.tipeTransaksi === "RUPIAH"
          ? bayarTrfRp
              .map((list) => list.transfer_netto)
              .reduce((a, b) => a + b, 0)
          : localStorage.getItem("totalTrfRp") || 0,
      detail_giro:
        this.state.tipeTransaksi === "RUPIAH" ? bayarGiroRp : bayarGiro,
      bayar_giro:
        this.state.tipeTransaksi === "RUPIAH"
          ? bayarGiroRp
              .map((list) => list.giro_netto)
              .reduce((a, b) => a + b, 0)
          : localStorage.getItem("totalGiro") || 0,
      
      detail_faktur: JSON.parse(localStorage.getItem("FakturTerpilih")),
    };
    localStorage.setItem("notaPembayaranSupplier", JSON.stringify(isi));
    // console.log(JSON.stringify(isi));
    // return false
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    let tableRows = [];
    if (this.state.tipeTransaksi === "RUPIAH") {
      tableRows = [
        [
          "Tunai",
          "Tunai",
          {
            content: formatRp(
              bayarTunaiRp !== null ? bayarTunaiRp[0].cash_rp : 0
            ),
            styles: { halign: "right" },
          },
          {
            content: formatRp(
              bayarTunaiRp !== null ? bayarTunaiRp[0].harga_emas : 0
            ),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatRp(
              bayarTunaiRp !== null ? bayarTunaiRp[0].cash_netto : 0
            ),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
        [
          "Transfer",
          "Transfer",
          {
            content: formatRp(
              bayarTrfRp !== null ? bayarTrfRp[0].transfer_rp : 0
            ),
            styles: { halign: "right" },
          },
          {
            content: formatRp(
              bayarTrfRp !== null ? bayarTrfRp[0].harga_emas : 0
            ),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatRp(
              bayarTrfRp !== null ? bayarTrfRp[0].transfer_netto : 0
            ),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
        [
          "Giro",
          "Giro",
          {
            content: formatRp(
              bayarGiroRp !== null ? bayarGiroRp[0].giro_rp : 0
            ),
            styles: { halign: "right" },
          },
          {
            content: formatRp(
              bayarGiroRp !== null ? bayarGiroRp[0].harga_emas : 0
            ),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatRp(
              bayarGiroRp !== null ? bayarGiroRp[0].giro_netto : 0
            ),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
        [
          "Titip",
          "Titip",
          {
            content: formatRp(
              bayarTitipRp !== null ? bayarTitipRp[0].titip_rp : 0
            ),
            styles: { halign: "right" },
          },
          {
            content: formatRp(
              bayarTitipRp !== null ? bayarTitipRp[0].harga_emas : 0
            ),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatRp(
              bayarTitipRp !== null ? bayarTitipRp[0].titip_netto : 0
            ),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
      ];
    } else {
      tableRows = [
        [
          "Tunai",
          "Tunai",
          {
            content: formatRp(bayarTunai !== null ? bayarTunai[0].cash_rp : 0),
            styles: { halign: "right" },
          },
          {
            content: formatRp(
              bayarTunai !== null ? bayarTunai[0].harga_emas : 0
            ),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatRp(
              bayarTunai !== null ? bayarTunai[0].cash_netto : 0
            ),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
        [
          "Transfer",
          "Transfer",
          {
            content: formatRp(bayarTrf !== null ? bayarTrf[0].transfer_rp : 0),
            styles: { halign: "right" },
          },
          {
            content: formatRp(bayarTrf !== null ? bayarTrf[0].harga_emas : 0),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatRp(
              bayarTrf !== null ? bayarTrf[0].transfer_netto : 0
            ),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
        [
          "Giro",
          "Giro",
          {
            content: formatRp(bayarGiro !== null ? bayarGiro[0].giro_rp : 0),
            styles: { halign: "right" },
          },
          {
            content: formatRp(bayarGiro !== null ? bayarGiro[0].harga_emas : 0),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatRp(bayarGiro !== null ? bayarGiro[0].giro_netto : 0),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
        [
          "Titip",
          "Titip",
          {
            content: formatGram2(bayarTitip !== null ? bayarTitip[0].titip_rp : 0),
            styles: { halign: "right" },
          },
          {
            content: formatGram2(
              bayarTitip !== null ? bayarTitip[0].harga_emas : 0
            ),
            styles: { halign: "right" },
          },
          "",
          {
            content: formatGram2(
              bayarTitip !== null ? bayarTitip[0].titip_netto : 0
            ),
            styles: { halign: "right" },
          },
          "",
          "",
        ],
      ];
    }
    var ciok = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciok.forEach((ticket, i) => {
      const ticketData = [
        "Ciok",
        ticket.kode_jenis,
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        "",
        { content: ticket.kadar_transaksi, styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var rongsok = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsok.forEach((ticket, i) => {
      const ticketData = [
        "Rongsok",
        ticket.kode_jenis,
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        "",
        { content: ticket.kadar_transaksi, styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var denda = [
      "DENDA",
      "DENDA",
      {
        content: formatGram(
          localStorage.getItem("denda") !== null
            ? localStorage.getItem("denda")
            : 0
        ),
        styles: { halign: "right" },
      },
      "",
      "",
      {
        content: formatGram(
          localStorage.getItem("denda") !== null
            ? localStorage.getItem("denda")
            : 0
        ),
        styles: { halign: "right" },
      },
      "",
      "",
    ];
    tableRows.push(denda);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      [
        "Jenis \nPembayaran",
        "Nama \nBarang",
        { content: "Gross \nBayar", styles: { halign: "right" } },
        { content: "Harga", styles: { halign: "right" } },
        { content: "Cap Kode", styles: { halign: "right" } },
        { content: "Netto", styles: { halign: "right" } },
        { content: "Pkg", styles: { halign: "right" } },
        { content: "Gw+Pkg", styles: { halign: "right" } },
      ],
    ];
    // AKHIR INISIALISASI AUTOTABLE
    console.log(JSON.stringify(isi));
    // return false;
    AxiosMasterPost("trx-customer/bayar-piutang", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() =>
            multipleDeleteLocal([
              "PrintPembayaranSupplier",
              "totalTunaiRp",
              "alatBayarTunai",
              "alatBayarTitipRp",
              "alatBayarTitip",
              "totalTrfRp",
              "alatBayarTrf",
              "ciokTemp",
              "alatBayarCiok",
              "totalCiok",
              "totalBrutoCiok",
              "rongsokTemp",
              "alatBayarRosok",
              "totalRongsok",
              "totalBrutoRongsok",
              "listHutang",
              "discount",
              "bonus",
              "denda",
              "cashTempRp",
              "alatBayarTunaiRp",
              "transferTempRp",
              "alatBayarTrfRp",
              "noFaktur",
              "alatBayarGiroRp",
              "totalGiroRp",
              "alatBayarGiro",
              "totalGiro",
            ])
          )
          .then(() => this.props.dispatch(getUUID()))
          .then(() => localStorage.setItem("isLogin", true))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.history.push("KlasifikasiSupplier"))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor\nPembayaran",
              data.noBon,
              "",
              "",
              getUserID(),
              getToday(),
              "",
              columTable,
              "NOTA PEMBAYARAN CUSTOMER",
              tableRows
            )
          )
          .then(() => window.history.back())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: err?.response.data || "ERROR PRINT BUKTI",
          showConfirmButton: false,
          timer: 3000,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }

  getHutang(data) {
    this.props.dispatch(setTitipCustomer({customer: data}))
    AxiosMasterGet(
      `trx-customer/get/piutang-titipan-customer/${getToday()}&${data}`
    )
      .then((res) => {
        this.setState({
          hutangnya: res.data,
        });
      })
      .then(() => {
        this.setState({
          listBon: this.state.hutangnya?.filter(
            (fil) =>
              fil.keterangan !== "TITIPAN RUPIAH" &&
              fil.keterangan !== "TITIPAN GRAM"
          ),
        });
      });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ol className="breadcrumb mb-2">
          <Link to="/Klasifikasicustomer">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Pembayaran Piutang Customer" />
              <div className="card-body">
                {this.FormPembayaranCustomer()}
                {this.FormPilihBon()}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  FormPembayaranCustomer() {
    return (
      <div className={this.state.formPembayaran}>
        <div className="col-lg-12">
          <ul className="nav nav-pills bg-nav-pills nav-justified mb-3">
            <li className="nav-item">
              <a
                href="#rupiah"
                data-toggle="tab"
                aria-expanded="false"
                className="nav-link rounded-0 active"
                onClick={() =>
                  this.setState({
                    tipeTransaksi: "BARANG",
                  })
                }
              >
                <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                <span className="d-none d-lg-block">Gram</span>
              </a>
            </li>
            <li className="nav-item">
              <a
                href="#gram"
                data-toggle="tab"
                aria-expanded="true"
                className="nav-link rounded-0 "
                onClick={() =>
                  this.setState({
                    tipeTransaksi: "RUPIAH",
                  })
                }
              >
                <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                <span className="d-none d-lg-block">Rupiah</span>
              </a>
            </li>
          </ul>
          <div className="tab-content">
            <div className="tab-pane show active" id="rupiah">
              <Suspense fallback={<Skeleton width={"100%"} height={200} />}>
                <PembayaranCustomerHead
                  tampungCiok={this.props.tampungCiok}
                  tampungRongsok={this.props.tampungRongsok}
                  totalCiok={this.props.totalCiok}
                  totalRongsok={this.props.totalRongsok}
                  totalBrutoCiok={this.props.totalBrutoCiok}
                  totalBrutoRongsok={this.props.totalBrutoRongsok}
                  onSubmit={(data) => this.handleSubmit(data)}
                  jenisTransaksi={this.state.tipeTransaksi}
                  onSend={this.props.onSend}
                  total_netto={this.state.total_netto}
                  total_rp={this.state.total_rp}
                />
              </Suspense>
            </div>
            <div className="tab-pane" id="gram">
              <Suspense fallback={<Skeleton width={"100%"} height={200} />}>
                <PembayaranCustomerHead
                  tampungCiok={this.props.tampungCiok}
                  tampungRongsok={this.props.tampungRongsok}
                  totalCiok={this.props.totalCiok}
                  totalRongsok={this.props.totalRongsok}
                  totalBrutoCiok={this.props.totalBrutoCiok}
                  totalBrutoRongsok={this.props.totalBrutoRongsok}
                  onSubmit={(data) => this.handleSubmit(data)}
                  jenisTransaksi={this.state.tipeTransaksi}
                  onSend={this.props.onSend}
                  total_netto={this.state.total_netto}
                  total_rp={this.state.total_rp}
                />
              </Suspense>
            </div>
          </div>
        </div>
      </div>
    );
  }
  FormPilihBon() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        let array = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
        const data = {
          no_faktur: row.no_ref,
          keterangan: row.keterangan,
          putus_titip: 0,
          harga_emas: 900000,
          putus_titip_netto: 0,
        };
        if (isSelect) {
          var index1 = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          if (index1 < 0) {
            array.push(data);
            this.setState({
              total_netto:
                parseFloat(this.state.total_netto) +
                parseFloat(row.sisa_piutang_netto),
              total_rp:
                parseFloat(this.state.total_rp) +
                parseFloat(row.sisa_piutang_rp),
            });
            console.log("MENAMBAHKAN NETTO", row.sisa_piutang_netto);
            console.log("MENAMBAHKAN RP", row.sisa_piutang_rp);
            this.props.dispatch(
              setTotalNetto(
                parseFloat(this.state.total_netto) +
                  parseFloat(row.sisa_piutang_netto)
              )
            );
            this.props.dispatch(
              setTotalRp(
                parseFloat(this.state.total_rp) +
                  parseFloat(row.sisa_piutang_rp)
              )
            );
          } else {
            array.splice(index1, 1);
            this.setState({
              total_netto:
                parseFloat(this.state.total_netto) -
                parseFloat(row.sisa_piutang_netto),
              total_rp:
                parseFloat(this.state.total_rp) -
                parseFloat(row.sisa_piutang_rp),
            });
            console.log("MENAMBAHKAN NETTO", row.sisa_piutang_netto);
            console.log("MENAMBAHKAN RP", row.sisa_piutang_rp);
            this.props.dispatch(
              setTotalNetto(
                parseFloat(this.state.total_netto) -
                  parseFloat(row.sisa_piutang_netto)
              )
            );
            this.props.dispatch(
              setTotalRp(
                parseFloat(this.state.total_rp) -
                  parseFloat(row.sisa_piutang_rp)
              )
            );
          }
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
        } else {
          var index = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          array.splice(index, 1);
          this.setState({
            total_netto:
              parseFloat(this.state.total_netto) -
              parseFloat(row.sisa_piutang_netto),
            total_rp:
              parseFloat(this.state.total_rp) - parseFloat(row.sisa_piutang_rp),
          });
          this.props.dispatch(setTotalNetto(this.state.total_netto));
          this.props.dispatch(setTotalRp(this.state.total_rp));
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        let array = [];
        let total_netto = 0;
        let total_rp = 0;
        rows.forEach(function (list) {
          const data = {
            no_faktur: list.no_ref,
            keterangan: list.keterangan,
            putus_titip: 0,
            harga_emas: 900000,
            putus_titip_netto: 0,
          };
          total_netto =
            parseFloat(total_netto) + parseFloat(list.sisa_piutang_netto);
          total_rp = parseFloat(total_rp) + parseFloat(list.sisa_piutang_rp);
          array.push(data);
        });
        if (isSelect) {
          this.setState({
            total_netto: total_netto,
            total_rp: total_rp,
          });
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("total_netto", total_netto);
          localStorage.setItem("total_rp", total_rp);
          this.props.dispatch(setTotalNetto(total_netto));
          this.props.dispatch(setTotalRp(total_rp));
        } else {
          this.setState({
            total_netto: 0,
            total_rp: 0,
          });
          localStorage.removeItem("FakturTerpilih");
          localStorage.removeItem("total_netto");
          localStorage.removeItem("total_rp");
          this.props.dispatch(setTotalNetto(0));
          this.props.dispatch(setTotalRp(0));
        }
      },
    };
    return (
      <div className={this.state.pilihBon}>
        <div className="col-md-4 col-lg-4">
          <Field
            name="customer"
            label="Customer"
            className="form-control"
            component={renderFieldSelect}
            options={this.props.customer.map((list) => {
              let data = {
                value: list.kode_customer,
                name: list.nama_customer,
              };
              return data;
            })}
            defaultValue="DEFAULT | DEFAULT"
            onChange={(data) => this.getHutang(data)}
          ></Field>
        </div>
        <div className="col-lg-12 mt-3">
          <GlobalTabel
            keyField="no_bon"
            data={this.state.listBon || []}
            columns={this.state.columns}
            search
            striped
            hover
            condensed
            bordered={true}
            headerClasses="header-table"
            pagination={paginationFactory()}
            selectRow={selectRow}
          />
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button
              className="btn btn-primary"
              onClick={() => {
                this.props.change("totalHutangRP", this.state.total_rp);
                this.props.change("totalHutangGr", this.state.total_netto);
                this.setState({
                  formPembayaran: "row",
                  pilihBon: "col-lg-12 d-none",
                });
              }}
            >
              Next
              <i className="fa fa-chevron-right ml-3"></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

PembayaranCustomer = reduxForm({
  form: "PembayaranCustomer",
})(PembayaranCustomer);
export default connect(maptoState, null)(PembayaranCustomer);
